<template>
  <!-- 一条文章单元格 -->
  <div>
  <van-cell>
    <!-- 标题区域的插槽 -->
    <template #title>
      <div class="title-box">
        <!-- 标题 -->

        <span>{{artObj.title}}</span>
        <img class="thumb" v-if="artObj.cover.type===1" v-lazy="artObj.cover.images[0]" alt="">
      </div>
      <div class="thumb-box" v-if="artObj.cover.type>1">
        <img class="thumb" v-for="(imgUrl,index) in artObj.cover.images" :key="index" v-lazy="imgUrl" alt="">
      </div>
    </template>
    <!-- label 区域的插槽 -->
    <template #label>
      <div class="label-box">
        <div>
          <span>{{artObj.aut_name}}</span>
          <span>{{artObj.comm_count}}评论</span>
          <span>{{artObj.pubdate}}</span>
        </div>
        <!-- 反馈按钮 -->
        <van-icon name="cross" @click.stop="show=true" v-if="isShow"/>
      </div>
    </template>
  </van-cell>
  <van-action-sheet
   v-model="show"
   :actions="actions"
   @select="onSelect"
   @cancel="cancelFn"
   :cancel-text="bottomText"
   @close="closeFn"
   get-container="body" />
  </div>
</template>

<script>
import { firstActions, secondActions } from '@/api/report.js'
export default {
  props: {
    artObj: Object,
    isShow: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      show: false,
      actions: firstActions,
      bottomText: '取消哦'
    }
  },
  methods: {
    // 反馈面板点击事件
    onSelect (action, item) {
      // console.log(action)
      // console.log(item)
      // this.show = false
      if (action.name === '反馈垃圾内容') {
        this.actions = secondActions
        this.bottomText = '返回哈'
      } else if (action.name === '不感兴趣') {
        this.$emit('dislikeEV', this.artObj.art_id)
        this.show = false
      } else {
        this.$emit('reportEV', this.artObj.art_id, action.value)
        this.show = false
      }
    },
    cancelFn () {
      if (this.bottomText === '返回哈') {
        this.show = true
        this.actions = firstActions
        this.bottomText = '取消哦'
      }
    },
    closeFn () {
      this.actions = firstActions
      this.bottomText = '取消哦'
    }
  }
}
</script>

<style scoped lang="less">
/* 标题样式 */
.title-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

/* label描述样式 */
.label-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* 文章信息span */
.label-box span{
    margin: 0 3px;
    &:first-child{
        margin-left: 0;
    }
}
/* 图片的样式, 矩形黄金比例：0.618 */
.thumb {
  width: 113px;
  height: 70px;
  background-color: #f8f8f8;
  object-fit: cover;
}

/* 三图, 图片容器 */
.thumb-box {
  display: flex;
  justify-content: space-between;
}
</style>
